import React from 'react'
import styled from 'styled-components';

const styles = {
    card: { 
        boxShadow: '0px 0px 5px grey',
        padding: '5px 10px',
        background: 'white',
        width: '90%',
        margin: 'auto',
        marginBottom: '15px',
    }
}
const Container = styled('div')(styles.card)
const Card = ({children}) => {
    return (
        <Container>
            {children}
        </Container>
    )
}

export default Card
