import React from 'react'
import styled from 'styled-components';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom'
const styles = {
    container: {
        padding: '10px 0px'
    },
    styledList: {
        textAlign: 'left',
        listStyleType: 'none',
        padding:'0px 0px'
    },
    styledListItem: {
        padding: '10px 10px',
        '&:hover': {
            backgroundColor: '#80808052',
            cursor:'pointer'
        }
    },
    link : {
        textDecoration: 'none',
        color:'black'
    }
}
const StyledList = styled('ul')(styles.styledList)
const Container = styled('div')(styles.container)
const StyledListItem = styled('li')(styles.styledListItem)
const StyledLink = styled(Link)(styles.link);
const MenuItems = (props) => {
    return (
        <Container>
            <StyledList>
                <StyledLink to="/orders"><StyledListItem onClick={props.clicked} >Your Orders</StyledListItem></StyledLink>
                <StyledListItem onClick={props.clicked}>Your address</StyledListItem>
                <StyledListItem onClick={props.clicked}>Your Account</StyledListItem>
                <StyledListItem onClick={props.clicked}>Settings</StyledListItem>
                {props.isAuthenticated ? 
                    <StyledLink to="/logOut"><StyledListItem >LogOut</StyledListItem></StyledLink> 
                    : <StyledLink to="/login"><StyledListItem>SignIn</StyledListItem></StyledLink>
                }
        
            </StyledList>
        </Container>
    )
}

const mapStatetoProps = state => {
    return {
        isAuthenticated: state.customerReducer.token !== null
        
    }

}

export default connect(mapStatetoProps)(MenuItems)
