import React, {useState} from 'react'
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import styled from 'styled-components';
import { device } from '../mediaQueries/device';
import { connect } from 'react-redux';
import * as actionCreators from '../store/actions/index';

const styles = {
    search: {
        display: 'flex',
        alignItems: 'center',
        width: '90%',
        fontWeight: '700',
    },
    searchIcon: {
        backgroundColor: '#febd69',
        outline:'none',
        border: 'none',
        padding: '6px 9px',
        '&:hover': {
            cursor: 'pointer'
        },
        [device.mobileS]: {
            padding: '4px 6px',
         }
    },
    input: {
        width: '100%',
        height:'33px',
        outline: 'none',
        fontWeight: '500',
        fontSize: '16px',
        border:'none',
        '&:focus' : {
            backgroundColor: '#e6f7ff'
        },
        [device.mobileS]: {
            width: '80%',
            height: '33px',
         },
         [device.tablet]: {
            width: '100%',
         },
         [device.desktop]: {
            height: '35px',
         },
    },
}

const SearchDiv = styled('form')(styles.search);
const SearchIconDiv = styled('button')(styles.searchIcon)
const SearchInput = styled('input')(styles.input)

const SearchBar = React.memo(props => {
    const [search, setSearch] = useState('')

    const onChangeHandler = (e) => {
        setSearch(e.target.value)
    }

    const searchProducts = (e) => {
        e.preventDefault();
        if(search) {
            props.onsearchProducts(search)
        } else {
            props.onFetchAllProducts()
        }
        
    }

    return (
        <SearchDiv>
            <SearchInput type="text" value={search} onChange={onChangeHandler} />
                <SearchIconDiv type="submit" onClick={searchProducts}>
                        <SearchRoundedIcon  />
                </SearchIconDiv>
        </SearchDiv>
    )
})

const mapDispatchtoProps = dispatch => {
    return {
        onsearchProducts: (product) => dispatch(actionCreators.searchProducts(product)),
        onFetchAllProducts: () => dispatch(actionCreators.fetchProduct())
    }
}

export default connect(null, mapDispatchtoProps)(SearchBar)

