import * as actionTypes from './actionTypes';
import axios from '../../Axios';

export const setClientSecret = (clientSecret) => {
    return {
        type: actionTypes.SET_CLIENT_SECRET,
        clientSecret
    }
}

export const getClientSecret = (total) => {
    const totalAmount = total * 100;
    return dispatch => {
        axios.post('/payments', {total: totalAmount})
            .then(res => {
                dispatch(setClientSecret(res.data.clientSecret))
            }).catch(error => {
                console.log(error)
            })
    }
}

export const setOrder = (orders) => {
    return {
        type: actionTypes.SET_ORDER,
        orders
    }
}

export const emptyBasket = () => {
    
    return {
        type: actionTypes.EMPTY_BASKET,
        
    }
}

export const emptySelectedAddress = () => {
    return {
        type: actionTypes.EMPTY_SELETED_ADDRESS,
        
    }
}


export const saveOrders = (orderId, basket, total, deliveryAddress, token) => {
    const config = {
        headers: {
            'content-type': 'application/json',
            Authorization: token
        }
    }
    
    return dispatch => {
        axios.post('/orders', {orderId,basket, total, deliveryAddress}, config)
            .then(res => {
                dispatch(emptySelectedAddress())
                dispatch(emptyBasket())
                .log(res.data)
                
            }).catch(error => {
                console.log(error)
            })
    }
}

export const getOrders = (token) => {
    const config = {
        headers: {
            'content-type': 'application/json',
            Authorization: token
        }
    }
    return dispatch => {
        axios.get('/orders', config)
            .then(res => {
                dispatch(setOrder(res.data))
              
            }).catch(error => {
                console.log(error)
            })
    }
}


export const setOrderId = (orderId) => {
    return {
        type: actionTypes.SET_ORDER_ID,
        orderId
    }
}

export const setOrderDetails = (orderdetails) => {
    return {
        type: actionTypes.SET_ORDER_DETAILS,
        orderdetails
    }
}

export const getOrderDetails = (orderId,token) => {
   
    const config = {
        headers: {
            'content-type': 'application/json',
            Authorization: token
        }
    }
    return dispatch => {
        axios.get(`/ordersdetails/${orderId}`, config)
            .then(res => {
                dispatch(setOrderDetails(res.data))
               
            }).catch(error => {
                console.log(error)
            })
    }
}