import React from 'react';
import styled from 'styled-components';
const styles = {
    styledDiv : {
        width:'50px',
        height:'40px',
        display: 'flex',
        flexFlow: 'column',
        justifyContent: 'space-around',
        alignItems: 'center',
        padding: '5px 0',
        boxSizing: 'border-box',
        
        border:'1px solid #eaeded73',
        '&:hover' : {
            cursor:'pointer',
            borderColor: 'white'
        }
    },
    innerstyledDiv: {
        width:'60%',
        height: '3px',
        backgroundColor: 'white'
    }
}
const StyledDiv = styled('div')(styles.styledDiv);
const InnerStyledDiv = styled('div')(styles.innerstyledDiv);
const DrawerToggle = (props) => {
    return (
        <StyledDiv onClick={props.clicked}>
            <InnerStyledDiv></InnerStyledDiv>
            <InnerStyledDiv></InnerStyledDiv>
            <InnerStyledDiv></InnerStyledDiv>
        </StyledDiv>
    )
}

export default DrawerToggle
