import React from 'react'
import { connect } from 'react-redux';
import styled from 'styled-components';
import numeral from 'numeral';
import Card from '../UI/Card';
import { useHistory } from 'react-router-dom';
import * as actionCreators from '../store/actions/index';

const styles = {
    container: {
        width: '95%',
        margin: 'auto'
    },
    orderList: {
        display: 'flex',
        marginTop: '20px',
        marginBottom: '20px'
    },
    item: {
        display: 'flex',
        justifyContent: 'space-between',
        width: '100%',
        marginTop: '20px',
        marginBottom: '20px'
    },
    itemdetails: {
        display: 'flex',
        flexDirection: 'column',
        marginLeft: '20px',
        lineHeight: '30px',
        alignContent: 'left',


    },
    quantity: {
        display: 'block',
        fontSize: '15px',
        fontWeight: '400',
        marginRight: '20px'
    },
    price: {
        display: 'block',
        fontSize: '15px',
        fontWeight: '400',
        marginRight: '20px'
    },
    datetime: {
        display: 'block',
        fontSize: '17px',
        fontWeight: '600',
        marginRight: '20px'
    },
    button: {
        width: 'max-content',
        fontSize:'12px',
        background: '#f0c14b',
        borderColor: '#a88734 #9c7e31 #846a29',
        color: '#111',
        borderRadius: '3px',
        borderStyle: 'solid',
        borderWidth: '1px',
        height: '35px',
        outline: 'none',
        '&:hover': {
            background: '#f2b211',
            cursor:'pointer'
        },
    }
}


const Item = styled('div')(styles.item);
const Button = styled('button')(styles.button)
const Itemdetails = styled('div')(styles.itemdetails);
const Quantity = styled('span')(styles.quantity);
const Price = styled('span')(styles.price);
const Datetime = styled('span')(styles.datetime);

const OrderItem = ( props) => {
    const history = useHistory(false)

    const showOrderDetails = () => {
        props.onsetOrderId(props.orderItem._id.orderId)
        history.push('/orderdetails')
        
    }

    return (
        <Card>
            <Item>
                <Itemdetails>
                    <Datetime>Order Id: {props.orderItem._id.orderId}</Datetime>
                    <Quantity>Total Items Qty: {props.orderItem.totalQuantity}</Quantity>
                    <Price>Sum Total Amount: &#x20B9;{numeral(props.orderItem.totalAmount).format('0,0.00')}</Price>
                </Itemdetails>
                <Button onClick={showOrderDetails}>Order Details</Button>
            </Item>
        </Card>
    )
}

const mapDispatchtoProps = dispatch => {
    return {
        onsetOrderId: (id) => dispatch(actionCreators.setOrderId(id))

    }
}



export default connect(null, mapDispatchtoProps)(OrderItem)
