import React from 'react'
import { device } from '../mediaQueries/device';
import styled from 'styled-components';
import numeral from 'numeral';
import Card from '../UI/Card';

const styles = {
    
    orderList: {
        display: 'flex',
        marginTop: '20px',
        marginBottom: '20px'
    },
    img: {
        objectFit: 'contain',
        [device.mobileS]: {
            height: '70px',
         },
    },
    item: {
        display: 'flex',
        justifyContent: 'space-between',
        width: '100%'
    },
    itemdetails: {
        display: 'flex',
        flexDirection: 'column',
        marginLeft: '20px',
        lineHeight: '30px',
        alignContent: 'left',
         

    },
    itemdescription: {
        color: '#0066c0',
        fontSize: '17px',
        fontWeight: '700',
        lineHeight: '30px',
        [device.mobileS]: {
            fontSize: '12px',
            fontWeight: '600',
            lineHeight: '20px',
         },
    },
    quantity: {
        display: 'block',
        fontSize: '13px',
        fontWeight: '600',
        marginRight: '20px'
    },
    price: {
        fontSize: '15px',
        fontWeight: '600',
        [device.mobileS]: {
            fontSize: '12px',
            fontWeight: '600',
         },
    },
    
}
const Item = styled('div')(styles.item);

const OrderListItem = styled('div')(styles.orderList);
const StyledImg = styled('img')(styles.img);
const Itemdetails = styled('div')(styles.itemdetails);
const ItemDescription = styled('span')(styles.itemdescription);
const Quantity = styled('span')(styles.quantity);

const Price = styled('span')(styles.price);

const OrderDetails = ({orderDetailsItem}) => {


    return (
        <Card>
            <OrderListItem>
                <StyledImg src={orderDetailsItem.productImage} />
                <Item>
                    <Itemdetails>
                        <ItemDescription>{orderDetailsItem.productName}</ItemDescription>
                        <Quantity>Qty: {orderDetailsItem.quantity}</Quantity>
                        <Price>&#x20B9;{numeral(orderDetailsItem.price).format('0,0.00')}</Price>
                        
                        
                    </Itemdetails>
                </Item>
            </OrderListItem>
        </Card>
    )
}



export default OrderDetails
