import React, {useState} from 'react'
import styled from 'styled-components';
import { connect } from 'react-redux';
import Mobile from '../login/Mobile';
import Name from '../login/Name';
import AddressLine from './AddressLine';
import Pincode from './Pincode';
import StateandCountry from './StateandCountry';
import { useHistory } from 'react-router-dom';
import * as actionCreators from '../store/actions/index';

const styles = {
    container: {
        border: '1px solid #dac8c8f7',
        borderRadius: '5px',
        display: 'block',
        padding: '20px',
        width: '90%',
        margin: '30px auto',
        fontSize: '13px',
        textAlign: 'left',
        lineHeight: '25px',
        height: '350px',
        overflowY: 'auto'
    },
    label: {
        display: 'block',
        fontWeight: '700'
    },
    styledbutton: {
        width: '96%',
        marginTop: '10px',
        height: '30px',
        background: '#f0c14b',
        borderColor: '#a88734 #9c7e31 #846a29',
        color: '#111',
        borderRadius: '3px',
        borderStyle: 'solid',
        borderWidth: '1px',
        cursor: 'pointer',
        '&:hover': {
            background: '#f2b211'
        }
    },
}
const Container = styled('form')(styles.container);
const Label = styled('label')(styles.label)
const StyledButton = styled('button')(styles.styledbutton);
const AddressForm = (props) => {
    const [name, setName] = useState('');
    const [mobile, setMobile] = useState(0);
    const [flat, setFlat] = useState('');
    const [area, setArea] = useState('');
    const [city, setCity] = useState('');
    const [country, setcountry] = useState('');
    const [state, setstate] = useState('');
    const [pin, setPin] = useState(0);
    const history = useHistory(false)
    //const [reset, setReset] = useState('')
    
    const nameValue = (value) => {
        setName(value);
    }

    const mobileValue = (value) => {
        setMobile(value);
    }

    const flatAddress = (value) => {
        setFlat(value);
    }

    const AreaAddress = (value) => {
        setArea(value);
    }

    const cityAddress = (value) => {
        setCity(value);
    }

    const pincode = (value) => {
        setPin(value);
    }

    const countryValue = (value) => {
        setcountry(value);
    }
    const stateValue = (value) => {
        setstate(value);
    }

    const formSubmitHandler = (e) => {
        e.preventDefault();
        
        props.onAddNewAddress(name, mobile, flat, area, city, country, state, pin, props.token)
        
        props.save();
    history.push('/reviewItems')
    }
    return (
        <Container>
            <Label>Full name</Label>
            <Name customername={nameValue} />
            <Label>Mobile Number</Label>
            <Mobile mobile={mobileValue}   />
            <StateandCountry country={countryValue} state={stateValue}  />
            <Label>Flat, House no., Building, Company, Apartment</Label>
            <AddressLine addressLine={flatAddress}  />
            <Label>Area, Colony, Street, Sector, Village</Label>
            <AddressLine addressLine={AreaAddress}  />
            <Label>City</Label>
            <AddressLine addressLine={cityAddress}  />
            <Label>PIN Code</Label>
            <Pincode pincode={pincode} />
            <StyledButton onClick={(e) =>formSubmitHandler(e)}>Save</StyledButton>
        </Container>
    )
}

const mapStatetoProps = state => {
    return {
        token: state.customerReducer.token,
        //allAddress: state.accountReducer.allAddress,
    }

}

const mapDispatchtoProps = dispatch => {
    return {
        onAddNewAddress: (name, mobile, flat, area, city, country, state, pin, token) => dispatch(actionCreators.addNewAddress(name, mobile, flat, area, city, country, state, pin, token))
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(AddressForm)
