import * as actionTypes from '../actions/actionTypes';

const initialState = {
    allAddress : [],
    deliveryAddress: {}
}


const accountReducer = (state = initialState, action) => {
    
    switch (action.type) {
        case actionTypes.SAVE_ADDRESS:
            return {
                ...state,
                allAddress: action.address
        };
        case actionTypes.SELECT_DELIVERY_ADDRESS:
            return {
                ...state,
                deliveryAddress: action.deliveryAddress
        };

        case actionTypes.EMPTY_SELETED_ADDRESS:
            return {
                ...state,
                deliveryAddress: {}
        };
        
        default: return state;
    }
}

export default accountReducer;