import React, {useState, useEffect} from 'react'
import styled from 'styled-components';
import Card from '../UI/Card';
import Modal from '../UI/Modal';
import AddressForm from './AddressForm';
import { connect } from 'react-redux';
import * as actionCreators from '../store/actions/index';
import AddressList from './AddressList';

const styles = {
    
    button: {
        width: 'max-content',
        fontSize:'12px',
        background: '#f0c14b',
        borderColor: '#a88734 #9c7e31 #846a29',
        color: '#111',
        borderRadius: '3px',
        borderStyle: 'solid',
        borderWidth: '1px',
        
        //height: '35px',
        outline: 'none',
        '&:hover': {
            background: '#f2b211',
            cursor: 'pointer',
        },
    }
}

const Button = styled('button')(styles.button)
const Address = React.memo ((props) => {
    const [modal, setModal] = useState(false)
    const [reset, setReset] = useState(false)
    //const addressArray = props.allAddress
    
    useEffect(() => {
        props.ongetAddress(props.token);
    }, [])


    const openModal = () => {
        setReset(false);
        setModal(true);
        
    }
    const closeModal = () => {
        setReset(true);
        setModal(false)
        
    }
    const proceed = () => {
        setModal(false);
        //history.replace('/payment')
    }
    return (
        <Card> 
            <h3>My Address</h3>
            <Modal show={modal} handleClose={closeModal} CloseMessage="Close">
                <h2>Add New Address</h2>
                <AddressForm save={proceed} reset={reset} />
            </Modal>

            {props.allAddress.length > 0 ? <AddressList address= {props.allAddress}/> :  null}
            
            <Button  onClick={openModal}>Add Address</Button>
        </Card>
    )
})

const mapStatetoProps = state => {
    
    return {
        token: state.customerReducer.token,
        allAddress: state.accountReducer.allAddress,
    }

}

const mapDispatchtoProps = dispatch => {
    return {
        ongetAddress: (token) => dispatch(actionCreators.getAddress(token))
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(Address)
