import React, {useState} from 'react'
import { device } from '../mediaQueries/device';
import styled from 'styled-components';
import { connect } from 'react-redux';
import * as actionCreators from '../store/actions/index';

const styles = {
    container: {
        border: '1px solid #dac8c8f7',
        borderRadius: '5px',
        display: 'block',
        padding: '15px',
        fontSize: '13px',
        textAlign: 'left',
        marginRight: '10px',
        marginBottom: '10px',
        //lineHeight: '25px',
        height: '120px',
        width: '200px',
        //overflowY: 'auto'
        '&.active': {
            backgroundColor : "#89c8e570"
        },
        '&.inactive': {
            backgroundColor : "white"
        }
    },
    name: {
        fontWeight: '600',
        display: 'block'
    },
    styleddiv: {
        display: 'block'
    },
    delete: {
        fontSize: '13px',
        color: '#0066C0',
        fontWeight: '600',
        marginRight: '20px',
        cursor: 'pointer',
        '&:hover': {
            color: '#ce760af7',
            textDecoration: 'underline'
        },
        [device.mobileS]: {
            fontSize: '12px',
            fontWeight: '400',
            marginRight: '7px',
        },
    },
}
let Container = styled('div')(styles.container);
const Name = styled('span')(styles.name);
const StyledDiv = styled('span')(styles.styleddiv);
const Delete = styled('span')(styles.delete);

const AddressItem = ( props) => {
    //const [classname, setClassname] = useState(props.active);
    
    const selectAddress = (selectedAddress) => {
        //setClassname("active");
        props.setInactive(selectedAddress._id);
        props.onSelectAddress(selectedAddress)
        
        
    }

    return (
        <Container className={props.active} >
            <div>
                <Name>{props.address.name}</Name>
                <StyledDiv>Phone Number: {props.address.mobile}</StyledDiv>
                <span>{props.address.flat}, </span>
                <span>{props.address.area}</span>
                <StyledDiv>{props.address.city}</StyledDiv>
                <span>{props.address.state} - </span>
                <span>{props.address.pin}</span>
                <StyledDiv>{props.address.country}</StyledDiv>
            </div>

            <div>
                <Delete
                 onClick={() => selectAddress(props.address)}
                >Deliver to this Address</Delete>
                <Delete
                 //onClick={() => props.onRemoveProduct(address._id, props.token)}
                >Delete</Delete>
            </div>
        </Container>
    )
}

const mapStatetoProps = state => {
    
    return {
        token: state.customerReducer.token,
        allAddress: state.accountReducer.allAddress,
        
    }

}

const mapDispatchtoProps = dispatch => {
    return {
        onRemoveProduct: (id, token) => dispatch(actionCreators.removeProductDb(id, token)),
        onSelectAddress: (selectedAddress) => dispatch(actionCreators.selectDeliveryAddress(selectedAddress))
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(AddressItem)
