import React, {useState} from 'react'
import styled from 'styled-components';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { CountryDropdown, RegionDropdown, CountryRegionData } from 'react-country-region-selector';

const styles = {
    styledinput: {
        width: '94%',
        padding: '5px 7px',
        margin:'auto',
        marginBottom: '10px',
        border:'1px solid #a6a6a6',
        borderRadius: '3px',
        outline: '0',
        height: '30px',
        lineHeight: 'normal'
    },
    error: {
        display: 'block',
        fontWeight: '500',
        textAlign: 'left',
        lineHeight: '0px',
        color: '#cc0808',
        marginBottom: '10px'
    },
    label: {
        display: 'block',
        fontWeight: '700'
    },
}
const StyledCountry = styled(CountryDropdown)(styles.styledinput);
const StyledState = styled(RegionDropdown)(styles.styledinput);
const StyledError = styled('span')(styles.error);
const Label = styled('label')(styles.label)

const  StateandCountry = (props)  => {
    const [countryinput, setCountryinput] =  useState('');
    const [stateinput, setStateinput] =  useState('');

    const formik = useFormik({
        initialValues: {
            country: '',
            state: ''
        },
        validationSchema: Yup.object({
            country: Yup.string()
                .required('Required'),
            state: Yup.string()
                .required('Required')

        })
    });

    const countryInputChangeHandler = (e) => {
        setCountryinput(e)
    }

    const stateInputChangeHandler = (e) => {
        setStateinput(e)
    }
    const countrySaveValue = () => {
        props.country(countryinput)
        
    }

    const stateSaveValue = () => {
        props.state(stateinput)
        
    }
    return (
        <div>
            <Label>Country/Region</Label>
            <StyledCountry 
                value={countryinput}
                onChange={(val) => {countryInputChangeHandler(val); }} 
                onBlur={(e) => {countrySaveValue();}} />
                {formik.touched.country && formik.errors.country ? (
                <StyledError>{formik.errors.country}</StyledError>
            ) : null}
            <Label>State/Province/Region</Label>
            <StyledState   
                country={countryinput}
                value={stateinput}
                onChange={(e) => {stateInputChangeHandler(e);}} 
                onBlur={(e) => {stateSaveValue(); }} />
                {formik.touched.state && formik.errors.state ? (
                <StyledError>{formik.errors.state}</StyledError>
            ) : null}
            
        </div>
    )
}

export default StateandCountry
