import React, {Component} from 'react'
import { connect } from 'react-redux';
import * as actionCreators from '../store/actions/index';
import styled from 'styled-components';
import OrderList from './OrderList';


class MyOrders extends Component  {
    componentDidMount () {
        this.props.ongetOrders(this.props.token)
        
    }

    
    render() {
        const styles = {
            container: {
                marginTop: '55px',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'left',
                textAlign: 'left',
                padding: '10px 10px',
            },
            heading: {
                marginLeft: '70px'
            },
        }
        const Heading = styled('div')(styles.heading);
        const Container = styled('div')(styles.container);
        return (
            <Container>
                 <Heading>
                    {!this.props.orders || !(this.props.isAuthenticated)
                        ? <h2>No Previous Orders</h2>
                        : <h2>Your Orders</h2>
                    }
                </Heading>
               
                {(this.props.orders) && (this.props.isAuthenticated)
                    && 
                    <OrderList orders ={this.props.orders} />
                    }
            </Container>
        )
    }
    
}

const mapStatetoProps = state => {
    
    return {
        token: state.customerReducer.token,
        orders: state.paymentReducer.orders,
        isAuthenticated: state.customerReducer.token !== null
    }
}

const mapDispatchtoProps = dispatch => {
    return {
        ongetOrders: (token) => dispatch(actionCreators.getOrders(token))

    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(MyOrders)
