import React from 'react'
import styled from 'styled-components';
import numeral from 'numeral';
import { connect } from 'react-redux';
import CartItemList from '../Checkout/CartItemList';
import Card from '../UI/Card';

const styles = {
    
    itemlistContainer: {
        display: 'flex',
        flexDirection: 'column',
        marginLeft: '20px',
        marginBottom : '33px'

    }

}
const ItemlistContainer = styled('div')(styles.itemlistContainer)
const ItemList = React.memo ((props) => {
    return (
        <Card>
            <h3>Review Items</h3>
            <ItemlistContainer>
                {props.basket.length > 0 ?
                <div>
                    {props.basket.map(element => {
                    return <CartItemList
                        key={element._id}
                        id={element._id}
                        product_id={element.product_id}
                        productName={element.productName}
                        productImage={element.productImage}
                        price={element.price}
                        quantity={element.quantity}
                    />
                })}
                <h3>Sum Total Amount: &#x20B9;{numeral(props.sumTotal).format('0,0.00')}</h3>
                </div>
                :
                <h3>Cart is empty. Add Products to Cart</h3>
                }
                
            </ItemlistContainer>

        </Card>
    )
})

const mapStatetoProps = state => {
    
    return {
        basket: state.basketReducer.basket,
        sumTotal: state.basketReducer.sumTotal,
    }

}

export default connect(mapStatetoProps)(ItemList)
