import * as actionTypes from './actionTypes';
import axios from '../../Axios';

export const saveAddress = (address) => {
    
    return {
        type: actionTypes.SAVE_ADDRESS,
        address
    }
}

export const selectDeliveryAddress = (deliveryAddress) => {
    
    return {
        type: actionTypes.SELECT_DELIVERY_ADDRESS,
        deliveryAddress
    }
}

export const getAddress = (token) => {
    const config = {
        headers: {
            'content-type': 'application/json',
            Authorization: token
        }
    }
    return dispatch => {
        axios.get('/getaddress', config)
            .then(res => {
                dispatch(saveAddress(res.data))
                
            }).catch(error => {
                console.log(error)
            })
    }
}



export const addNewAddress = (name, mobile, flat, area, city, country, state, pin, token) => {
    const config = {
        headers: {
            'content-type': 'application/json',
            Authorization: token
        }
    }
    
    return dispatch => {
        axios.post('/saveaddress', {name, mobile, flat, area, city, country, state, pin}, config)
            .then(res => {
                dispatch(getAddress(token))
                
            }).catch(error => {
                console.log(error)
            })
    }
}

