import React, {useState, useEffect} from 'react'
import { CardElement, useStripe, useElements } from '@stripe/react-stripe-js';
import styled from 'styled-components';
import Card from '../UI/Card';
import { connect } from 'react-redux';
import * as actionCreators from '../store/actions/index';
import { useHistory } from 'react-router-dom';
import Modal from '../UI/Modal'


const styles = {
    container: {
        width: '60%',
        margin: 'auto',
        marginTop: '15%',
        flex: '0.8'
    },
    label: {
        textAlign: 'left',
        fontFamily: 'sans-serif',
        fontWeight: '600'
    },
    button: {
        width: '40%',
        fontSize:'15px',
        background: '#f0c14b',
        borderColor: '#a88734 #9c7e31 #846a29',
        color: '#111',
        borderRadius: '3px',
        borderStyle: 'solid',
        borderWidth: '1px',
        marginTop: '30px',
        height: '35px',
        outline: 'none',
        '&:hover': {
            background: '#f2b211',
            cursor: 'pointer',
        },
        '&:disabled': {
            background: '#c8ad69b3',
            color: '#111111bd',
            cursor: 'default'
        }
    }
}

const Container = styled('div')(styles.container)
const Label = styled('h2')(styles.label)
const Button = styled('button')(styles.button)

const PaymentPortal = (props) => {
    const stripe = useStripe();
    const elements = useElements();
    const history = useHistory();
    const [disabled, setDisabled] = useState(true);
    const [processing, setProcessing] = useState(false);
    const [succeeded, setSucceeded] = useState(false);
    const [error, setError] = useState(null)
    const [modal, setModal] = useState(true);
        const closeModal = () => {
        setModal(false)
        history.replace('/orders')
    }
    const proceed = () => {
        setModal(false);
        history.replace('/orders')
    }

    const basketItem = props.basket;
    const orderId= Math.random();
    useEffect(() => {
        props.ongetClientSecret(props.sumTotal);
        
    }, [basketItem])


    const handleChange = (e) => {
        setDisabled(e.empty);
        setError(e.error ? e.error.message : "");
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setProcessing(true);
        const payload = await stripe.confirmCardPayment(props.clientSecret, {
            payment_method : {
                card: elements.getElement(CardElement)
            }
            
        }).then(({paymentIntent}) => {
            setSucceeded(true);
            setProcessing(false);
            setError(null)
            
            props.onSaveOrders(orderId,basketItem, props.sumTotal, props.deliveryAddress, props.token)
           
        }).catch( error => {
            console.log(error)
        })
    }
    
    return (
        <Container>
            {succeeded ? <Modal show={modal} handleClose={closeModal} proceed={proceed} ContinueMessage="Okay" > Your Order was Successfull</Modal> : null}
            <Card>
                <Label>Payment mode</Label>
                <form onSubmit= {handleSubmit}>
                    <CardElement onChange={handleChange} />
                    <Button  disabled = {processing || disabled || succeeded} >
                        {processing ? "Processing..." : "Buy Now"}
                    </Button>
                    {error && <div>{error}</div>}
                </form>
            </Card>
        </Container>
    )
}

const mapStatetoProps = state => {
    
    return {
        sumTotal: state.basketReducer.sumTotal,
        basket: state.basketReducer.basket,
        clientSecret: state.paymentReducer.clientSecret,
        token: state.customerReducer.token,
        deliveryAddress: state.accountReducer.deliveryAddress
    }
}

const mapDispatchtoProps = dispatch => {
    return {
        ongetClientSecret: (total) => dispatch(actionCreators.getClientSecret(total)),
        onSaveOrders: (orderId,basket, total, deliveryAddress, token) => dispatch(actionCreators.saveOrders(orderId,basket, total, deliveryAddress, token))
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(PaymentPortal)
