import React, { useState } from 'react'
import AddressItem from './AddressItem';
import styled from 'styled-components';
import { connect } from 'react-redux';

const styles = {
    container: {
        display: 'flex',
        flexDirection: 'row',
        overflowX: 'auto'
    },
}
const Container = styled('div')(styles.container);

const AddressList = (props) => {
    const [activeClass, setActiveClass] = useState("");

    const setInactive = (id) => {
        setActiveClass(id)
    }

    return (
        <Container>
            {props.address.map(element => {
                if (activeClass == element._id) {
                    return <AddressItem address={element} active="active" setInactive={setInactive} />
                } else {
                    return <AddressItem address={element} active="inactive" setInactive={setInactive} />
                }


            })}
        </Container>


    )
}

const mapStatetoProps = state => {

    return {
        deliveryAddress: state.accountReducer.deliveryAddress
    }

}

export default connect(mapStatetoProps)(AddressList)
