import React, { Component } from 'react'
import styled from 'styled-components';
import Card from '../UI/Card';
import * as actionCreators from '../store/actions/index';
import { connect } from 'react-redux';
import OrderDetails from './OrderDetails';

class OrderDetailsList extends Component {

    componentDidMount() {
        this.props.ongetOrderDetails(this.props.orderId, this.props.token)
        
    }

    render() {

        const styles = {
            container: {
                display: 'flex',
                flexDirection: 'row',
                width: '95%',
                margin: 'auto',

            },
            orderList: {
                display: 'flex',
                flexDirection: 'column',
                textAlign: 'left',
                marginTop: '55px',
                marginBottom: '20px',
                width: '100%'
            },
            addressdiv: {
                border: '1px solid #00000036',
                padding: '20px 10px',
                height: '200px',
                width: '150px',
                marginTop: '127px',
                marginLeft: '10px',
                textAlign: 'Left'
            },
            address: {
                fontSize: '12px',
                fontWeight: '500',
            }

        }

        const Container = styled('div')(styles.container);
        const OrderListItem = styled('div')(styles.orderList);
        const Address = styled('span')(styles.address);
        const AddressDiv = styled('div')(styles.addressdiv);

        return (
            <Container>

                <OrderListItem>
                    <h2>Order Details: </h2>
                    {this.props.orderDetails ?
                        this.props.orderDetails.map(item => {
                            return <OrderDetails orderDetailsItem={item} />
                        })
                        : null}
                </OrderListItem>
                {this.props.orderDetails && this.props.orderDetails[0] ?
                    <AddressDiv>
                        <Address>
                            Delivery Address:
                                    <br />
                        Name: {this.props.orderDetails[0].deliveryAddress.name}
                                <br />
                        Phone: {this.props.orderDetails[0].deliveryAddress.mobile}
                                <br />
                        City: {this.props.orderDetails[0].deliveryAddress.city}
                                <br />
                        State: {this.props.orderDetails[0].deliveryAddress.state}
                   

                </Address>
                    </AddressDiv> :
                    null
                }

            </Container>
        )
    }

}

const mapStatetoProps = state => {
    return {
        token: state.customerReducer.token,
        orderId: state.paymentReducer.orderId,
        orderDetails: state.paymentReducer.orderDetails,
    }
}

const mapDispatchtoProps = dispatch => {
    return {
        ongetOrderDetails: (orderId, token) => dispatch(actionCreators.getOrderDetails(orderId, token))

    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(OrderDetailsList)
