import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import productReducer from './store/reducers/productReducer';
import basketReducer from './store/reducers/basketReducer';
import customerReducer from './store/reducers/customerReducer';
import paymentReducer from './store/reducers/paymentReducer';
import accountReducer from './store/reducers/accountReducer';
import { BrowserRouter } from 'react-router-dom';
import { createStore, compose, applyMiddleware, combineReducers } from 'redux';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import { persistStore, persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import { PersistGate } from 'redux-persist/integration/react'



 const persistConfig = {
  key: 'root',
  storage,
  whitelist: ['customerReducer', 'basketReducer']
}


const rootReducer = combineReducers({
  productReducer,
  customerReducer,
  basketReducer,
  paymentReducer,
  accountReducer
})

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(persistReducer(persistConfig, rootReducer), composeEnhancers(
  applyMiddleware(thunk)
));


export const persistor = persistStore(store);

const app = (
  <Provider store={store}>
    <BrowserRouter>
      <React.StrictMode>
        <PersistGate persistor={persistor}>
          <App />
          </PersistGate>
        </React.StrictMode>
      
    </BrowserRouter>
  </Provider>
)
ReactDOM.render(app, document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
