import React from 'react'
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { device } from '../mediaQueries/device';

const styles = {
    button: {
        width: 'max-content',
        fontSize:'17px',
        background: '#f0c14b',
        borderColor: '#a88734 #9c7e31 #846a29',
        color: '#111',
        borderRadius: '3px',
        borderStyle: 'solid',
        borderWidth: '1px',
        
        height: '35px',
        outline: 'none',
        '&:hover': {
            background: '#f2b211',
            cursor: 'pointer',
        },
        // [device.mobileS]: {
        //     height: '20px',
        //  },
        //  [device.tablet]: {
        //     height: '25px'
        //  },
    }
}
const Button = styled('button')(styles.button);
const BuyProduct = (props) => {
    const history = useHistory();
    return (
        <div>
            <Button onClick={(e) => history.push('/reviewItems')}>Proceed to buy</Button>
        </div>
    )
}

export default BuyProduct
