import React from 'react'
import MenuItems from './MenuItems'
import styled from 'styled-components';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom'
const styles = {
    styledMenu: {
        position: 'fixed',
        width: '280px',
        maxWidth: '70%',
        height: '100%',
        top: '0',
        left: '0',
        zIndex: '300',
        background: 'white',
        boxSizing: 'border-box',
        transition: 'transform 0.3s ease-out',
        '&.Open': {
            transform: 'translateX(0)'
        },
        
        '&.Close' :{
            transform: 'translateX(-100%)'
        }
        
    },
    heading: {
        textAlign: 'left',
        backgroundColor: '#232f3e',
        color:'white',
        padding: '0px 10px',
        height:'50px',
        fontSize: '25px'
    },
    link : {
        textDecoration: 'none'
    }
}
const SideMenu = styled('div')(styles.styledMenu)
const Heading = styled('div')(styles.heading)
const StyledLink = styled(Link)(styles.link);
const Menu = (props) => {
    let attachedClasses ="Close"
    if(props.open) {
        attachedClasses = "Open"
    }
    return (
        <SideMenu className={attachedClasses}>
            {props.isAuthenticated ? <Heading>Hello, {props.name}</Heading> : <StyledLink to="/login"><Heading>Hello, SignIn</Heading></StyledLink>}
            <MenuItems clicked={props.clicked} />
        </SideMenu>
    )
}

const mapStatetoProps = state => {
    return {
        isAuthenticated: state.customerReducer.token !== null,
        name: state.customerReducer.name
        
    }

}

export default connect(mapStatetoProps)(Menu)
