import React, {useState} from 'react'
import { useHistory } from 'react-router-dom';
import numeral from 'numeral';
import styled from 'styled-components';
import Card from '../UI/Card';
import Modal from '../UI/Modal';
import { connect } from 'react-redux';

const styles = {
    
    button: {
        width: 'max-content',
        fontSize:'17px',
        background: '#f0c14b',
        borderColor: '#a88734 #9c7e31 #846a29',
        color: '#111',
        borderRadius: '3px',
        borderStyle: 'solid',
        borderWidth: '1px',
        
        height: '35px',
        outline: 'none',
        '&:hover': {
            background: '#f2b211',
            cursor: 'pointer',
        },
        '&:disabled': {
            background: '#cca94f80',
            cursor: 'default',
            color: '#111111b3'
        }
    }
} 
const Button = styled('button')(styles.button)

const Payment = (props) => {
    const [modal, setModal] = useState(false)
    const history = useHistory(false)

    const openModal = () => {
        
        setModal(true);
       
    }
    const closeModal = () => {
        setModal(false)
    }
    const proceed = () => {
        setModal(false);
        history.replace('/payment')
    }
    let basketItem = 0;
        const basketItemQuantity = props.basket.map(elm => {
            return elm.quantity;
        });

        if (basketItemQuantity.length > 0) {
            basketItem = basketItemQuantity.reduce((total, num) => {
                return Number(total) + Number(num)
            })
        }
    return (
        <Card>
            <Modal show={modal} handleClose={closeModal} proceed={proceed} ContinueMessage="Continue" CloseMessage="Cancel" >
                <h2>Delivery Details</h2>
                {Object.keys(props.deliveryAddress).length !== 0 ? 
                <div>
                    <h4>Address:</h4>
                    <span>{props.deliveryAddress.name}  </span>
                    <span>Phone: {props.deliveryAddress.mobile}</span>
                    <br />
                    <span>{props.deliveryAddress.flat}, </span>
                    <span>{props.deliveryAddress.area}, </span>
                    <span>{props.deliveryAddress.state} - </span>
                    <span>{props.deliveryAddress.pin}, </span>
                    <span>{props.deliveryAddress.country}</span>
                </div> :
                <p>Please select a delivery address</p>
                }
                {props.basket.length > 0 ? 
                <div>
                    <h4>Total Items:</h4>
                    <span>{basketItem}</span>
                    <h4>Total Amount:</h4>
                    <span>&#x20B9;{numeral(props.sumTotal).format('0,0.00')}</span>
                </div> :
                <p>Basket Empty</p>}
            </Modal>
            <Button onClick={openModal} disabled={Object.keys(props.deliveryAddress).length === 0 || props.basket.length === 0 } >Continue to Proceed</Button>
        </Card>
    )
}

const mapStatetoProps = state => {
    
    return {
        deliveryAddress:  state.accountReducer.deliveryAddress,
        basket: state.basketReducer.basket,
        sumTotal: state.basketReducer.sumTotal,
    }

}

export default connect(mapStatetoProps)(Payment)
