import React, { useState } from 'react'
import styled from 'styled-components';

const styles = {
    modal: {
        // display: `${props => props.showModal ? 'block' : 'none'}`,
        position: 'fixed',
        top: '0',
        left: '0',
        width: '100%',
        height: '100%',
        zIndex: '100',
        background: 'rgba(0, 0, 0, 0.6)'
    },
    section: {
        position: 'fixed',
        background: 'white',
        width: '40%',
        height: 'auto',
        top: '50%',
        left: '50%',
        padding: '10px 10px',
        transform: 'translate(-50%,-50%)',
    },
    buttonBox: {
        display: 'flex',
        justifyContent: 'center'
    },
    button: {
        width: 'max-content',
        fontSize: '17px',
        background: '#f0c14b',
        borderColor: '#a88734 #9c7e31 #846a29',
        color: '#111',
        borderRadius: '3px',
        borderStyle: 'solid',
        borderWidth: '1px',
        height: '35px',
        outline: 'none',
        marginRight: '10px',
        '&:hover': {
            background: '#f2b211',
            cursor: 'pointer',
        },
    }
}

const Button = styled('button')(styles.button)
const Section = styled('div')(styles.section)
const CustomModal = styled('div')(styles.modal)
const ButtonBox = styled('div')(styles.buttonBox)
const StyledModal = styled(CustomModal)`
display: ${props => props.showModal ? 'block' : 'none'}

`

const Modal = ({ handleClose, show, children, proceed, ContinueMessage, CloseMessage }) => {

    return (
        <StyledModal showModal={show}>
            <Section>
                {children}
                <ButtonBox>
                    {ContinueMessage && <Button onClick={proceed}>{ContinueMessage}</Button>}
                    {CloseMessage && <Button onClick={handleClose}>{CloseMessage}</Button>}
                    
                </ButtonBox>
            </Section>
        </StyledModal>
    )
}

export default Modal
